.gpt3__whatgpt3
{
    display: flex;
    flex-direction: column;
    padding: 2rem;

    /* ff 3.6+ */
    background:-moz-linear-gradient(171deg, rgba(2, 38, 66, 1) 0%, rgba(0, 85, 177, 1) 100%); 

    /* safari 5.1+,chrome 10+ */
    background:-webkit-linear-gradient(171deg, rgba(2, 38, 66, 1) 0%, rgba(0, 85, 177, 1) 100%);

    /* opera 11.10+ */
    background:-o-linear-gradient(171deg, rgba(2, 38, 66, 1) 0%, rgba(0, 85, 177, 1) 100%);

    /* ie 6-9 */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0055B1', endColorstr='#022642', GradientType=1 );

    /* ie 10+ */
    background:-ms-linear-gradient(171deg, rgba(2, 38, 66, 1) 0%, rgba(0, 85, 177, 1) 100%);

    /* global 94%+ browsers support */
    background:linear-gradient(171deg, rgba(2, 38, 66, 1) 0%, rgba(0, 85, 177, 1) 100%);

}
.gpt3__whatgpt3-feature {
    display: flex;
}
.gpt3__whatgpt3-feature .gpt3__features-container__feature {
    margin: 0;
}
.gpt3__whatgpt3-feature .gpt3__features-container__feature-text {
    max-width: 700px;
}
.gpt3__whatgpt3-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 4rem 0 2rem;
}
.gpt3__whatgpt3-header h1{
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
    max-width: 510px;
}
.gpt3__whatgpt3-header p{
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var(--color-subtext);
    cursor: pointer;
}
.gpt3__whatgpt3-container{
    display: flex;
    flex-wrap: wrap;
    margin-top: 2rem;

}
.gpt3__whatgpt3-container .gpt3__features-container__feature {
    flex: 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;
}

@media screen and (max-width: 850px) {
    .gpt3__whatgpt3-header {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .gpt3__whatgpt3-header p {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 650px) {
    /* Customizing feature component as per needs */
    .gpt3__whatgpt3-feature .gpt3__features-container__feature {
        flex-direction: column;
    }

    /* Customizing feature component as per needs */
    .gpt3__whatgpt3-feature .gpt3__features-container_feature-text {
        margin-top: 0.5rem;
    }

    .gpt3__whatgpt3-header h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {
    /* Customizing feature component as per needs */
    .gpt3__whatgpt3-container .gpt3__features-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
}