.gpt3__cta{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem;
    margin: 4rem;
    border-radius: 1rem;
    background: var(--gradient-bar);
}
.gpt3__cta-content p{
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 30px;
    color: #0E0E0E;
}
.gpt3__cta-content h3{
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 800;
    font-size: 24px;
    line-height: 45px;
    color: #000000;
}
.gpt3__cta-btn button{
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 30px;
    color: #FFFFFF;
    width: 189.14px;
    height: 59.4px;
    background: #000000;
    border-radius: 40px;
    cursor: pointer;
    border: none;
    outline: none;
}
.gpt3__cta-btn{
    display: flex;
    margin-left: 2rem;
    justify-content: center;
    align-items: center;
    border: none;
    outline: none;
}

@media screen  and (max-width:650px){
    .gpt3__cta{
        flex-direction: column;
    }
    .gpt3__cta-btn{
        margin: 2rem 0 0 ;
    }
}
@media screen  and (max-width:650px){
    .gpt3__cta{
        margin: 4rem 2rem;
    }
    .gpt3__cta-content h3{
        font-size:18px ;
        line-height: 32px;
    }
    .gpt3__cta-btn button{
        font-size:14px ;
        line-height: 28px;
    }
}